.b-layout {
  width: 100%;
  min-height: 100vh;
  flex-direction: column;
  display: flex;
  height: 100%;
  overflow-x: hidden;
  max-width: 2560px;
  margin: 0 auto; }
  .b-layout-main {
    flex: 1 0 auto;
    width: 100%; }
  .b-layout-footer {
    flex: none; }

.b-list {
  min-height: 150px; }
  .b-list-item {
    margin-bottom: 1rem; }

.react-datepicker {
  border-color: rgba(0, 0, 0, 0.125); }
  .react-datepicker__header {
    border-color: rgba(0, 0, 0, 0.125);
    background-color: #f7f7f7; }

.react-datepicker__day--selected,
.react-datepicker__day--in-selecting-range,
.react-datepicker__day--in-range {
  background-color: #007bff; }
  .react-datepicker__day--selected:hover,
  .react-datepicker__day--in-selecting-range:hover,
  .react-datepicker__day--in-range:hover {
    background-color: #0069d9; }

.b-item__title {
  cursor: pointer; }

.b-item__full-text {
  display: none; }
  .b-item__full-text.toogled {
    display: inline; }

.b-item__author {
  color: #bdbdbd;
  font-size: 13px; }

.b-item-comment {
  font-size: 12px;
  margin-bottom: 0.5em;
  border-bottom: 1px solid #f2f2f2;
  padding-bottom: 0.5em;
  /*&:last-child{
      border-bottom: none;
      margin: 0;
      padding: 0;
    }*/ }
  .b-item-comment__author {
    color: #999;
    font-size: 11px; }
  .b-item-comment-title {
    line-height: 1;
    margin-bottom: 0.5rem; }
  .b-item-comment-toggler {
    line-height: 1;
    display: inline-block;
    font-size: 12px;
    color: #5e5e5e;
    text-decoration: underline; }

.b-item__meta {
  color: #999;
  margin-bottom: 0.5rem; }
